.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*App style */

/*header*/
.header {
  height: 80px;
  width: 100%;
  position: fixed;
  display: flex;
  z-index: 99;
  background-color: white;
  padding: 0 16%;
  align-items: center;
  justify-content: space-between;
}

.logoImg {
  height: 60px;
}

/*home*/
.main-container {
  padding: 80px 16% 0;
  flex-direction: column;
}

.main-container div {
  display: flex;
}

.main-container h1,
.main-container h3,
.main-container h4,
.main-container h5 {
  margin: 4px 0;
}

.main-container h4 {
  color: gray;
}

.intro-container {
  margin: 30px 0 10px 0;
}

.pools-container {
  margin-top: 20px;
  width: 100%;
}

.common-bg {
  border-radius: 8px;
  background-color: rgba(0, 20, 200, 0.1);
}
.common-bg:hover {
  background-color: rgba(0, 20, 200, 0.2);
}

.tvl-box {
  flex-flow: column nowrap;
  width: 100%;
  justify-content: center;
}

.intro-item {
  text-align: center;
  width: 100%;
  min-width: 200px;
  justify-content: center;
}

.tvl-items {
  flex-flow: row nowrap;
  padding: 16px 20%;
  justify-content: space-between;
}

.tvl-item {
  flex-flow: column;
  text-align: center;
}

.blue-font {
  color: #054997;
}

.intro {
  text-align: center;
}

.boxes {
  padding: 20px 0;
  flex-flow: row wrap;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.miner-items {
  flex: 1 1 auto;
  min-width: 200px;
  flex-direction: column;
  padding: 4px 0;
  padding-left: 10%;
}

.token-name {
  display: flex;
}

/*footer*/
.footer {
  display: flex;
  padding: 60px 18% 40px;
  justify-content: space-between;
}

.footer a {
  font-size: 1.2rem;
  text-decoration: none;
  color: #666;
  font-weight: bold;
}

.forms-bg {
  flex: 1;
  /*margin: 10px;*/
  background-color: rgba(0, 20, 200, 0.1);
  border-radius: 8px;
}

.stakingInfo > div {
  flex-direction: column;
  flex: 1;
  padding: 2px 16px;
}

.stakingInfo1,
.stakingInfo2 {
  border-right: 1px solid #bbccee;
  border-bottom: 0;
}

.stakingInfo,
.stakingDetail {
  line-height: 0.8;
}

/*mobile*/
@media only screen and (max-width: 768px) {
  .header {
    padding: 0 4%;
  }

  .logoImg {
    height: 40px;
  }

  .main-container {
    padding: 80px 4% 0 !important;
  }

  .main-container h1 {
    font-size: 1.2rem;
  }

  .main-container h3 {
    font-size: 1rem;
  }

  .main-container h4 {
    font-size: 0.6rem;
  }

  .tvl-items {
    padding: 16px 0;
  }

  .miner-items2 {
    align-items: center;
    padding-left: 0 !important;
  }

  .footer {
    padding: 60px 10% 20px;
  }

  .footer a {
    font-size: 0.8rem;
  }

  .stakingInfo {
    padding: 10px 20px !important;
    margin: 10px !important;
  }
  .stakingInfo,
  .stakingDetail {
    flex-direction: column !important;
  }

  .stakingInfo > div {
    flex-direction: column;
    flex: 1;
    padding: 16px 2px;
  }

  .stakingInfo1,
  .stakingInfo2 {
    border-right: 0;
    border-bottom: 1px solid #bbccee;
  }
}

@media only screen and (min-width: 1000px) {
  .miner-items > button {
    width: 200px !important;
  }

  .miner-items1 {
    flex: 2;
  }
}

button {
  text-transform: none !important;
}

#simple-tab-0,
#simple-tab-1 {
  font-weight: bold;
  font-size: 1rem;
}
